* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #fafaf9;
  color: #fafaf9;
  font-family: "Sono", sans-serif;
  padding: 30px 64px;
  scroll-behavior: smooth;
}

/* NavBar Component */

.header {
  display: flex;
  text-align: center;
  justify-content: space-between;
  padding: 10px;
  font-family: "Coiny", sans-serif;
}

.logo-head {
  display: flex;
  text-align: center;
  gap: 16px;
}

.header h1 {
  text-transform: uppercase;
  color: #000;
  margin-top: 10px;
}

.logo-head img {
  width: 68px;
  height: 68px;
}

.btn {
  width: 150px;
  height: 50px;
  font-size: 15px;
  font-family: "Sono", sans-serif;
  border-radius: 100px;
  margin-right: 20px;
  border: none;
  transition: all 0.5s;
  color: #fff;
  background-color: #000;
  cursor: pointer;
}

.btn:hover {
  color: #fff;
  /* transform: scale(110%); */
  background-image: linear-gradient(135deg, #3b82f6, #ef4444, #16a34a, #eab308);
}

/* Form  */
.form {
  margin: 35px 20px;
  color: #000;
  font-family: "Sono", sans-serif;
  font-size: 12px;
}

.form textarea {
  outline: none;
  border-radius: 5px;
  font-size: 20px;
  font-family: inherit;
  padding: 5px;
  border: 2px solid black;
  width: 100%;
  box-sizing: border-box;
  resize: vertical;
}

.form textarea::placeholder {
  font-size: 20px;
}

h2:first-child {
  margin-top: 30px;
}

/* functions button */

.btn-fun {
  margin-top: 10px;
  font-size: 15px;
  margin-top: 25px;
}

.remove {
  width: 180px;
}

.hidden {
  display: none;
}

@media only screen and (max-width: 600px) {
  body {
    padding: 5px;
  }

  .btns {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    align-items: center;
    margin-left: 20px;
  }
  .header {
    padding: 5px;
    margin-top: 10px;
    margin-bottom: 0;
  }

  .header h1 {
    /* padding-top: 10px; */
    font-size: 19px;
  }
  .hidden {
    width: 50px;
    margin-left: auto;
    display: none;
  }
  .header img {
    width: 55px;
    height: 55px;
  }

  .form {
    margin-top: 25px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .btns {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: center;
    align-items: center;
  }

  .hidden {
    width: 150px;
  }
}

